.App {
  display: grid;
  grid-template: 5rem 1fr 7rem/1fr minmax(10rem, 30%);
  grid-gap: 10px;
  min-height: 100vh;
  grid-template-areas: "header header" "main  sidebar" "footer footer"; }
  .App > * {
    border: 1px solid black;
    border-radius: 5px;
    background-color: #eee; }
  .App > header {
    grid-area: header; }
  .App > main {
    grid-area: main; }
  .App > aside {
    grid-area: sidebar; }
  .App > footer {
    grid-area: footer; }
