.App{
  display: grid;
  grid-template: 5rem 1fr 7rem/ 1fr minmax(10rem , 30%);
  grid-gap: 10px;
  min-height: 100vh;
  grid-template-areas:  "header header"
                          "main  sidebar"
                          "footer footer";
  >*{
    border: 1px solid black;
    border-radius: 5px;
    background-color: #eee;
  }
  &>header{
    grid-area: header;
  }
  &>main{
    grid-area:main;
  }
  &>aside{
    grid-area: sidebar;
  }
  &>footer{
    grid-area: footer;
  }
}
